$(document).ready(function () {
    $('.owl-carousel-main').owlCarousel({
        margin: 10,
        loop: true,
        autoWidth: true,
        items: 1,
        dots: true,
        dotsContainer: '#customDots',
        navContainer: '#customNav',
        navText: ["<img src='img/prev.svg'>", "<img src='img/next.svg'>"],
    });
    $('.owl-carousel-mobile').owlCarousel({
        margin: 10,
        loop: true,
        items: 1,
        dots: true,
        nav: true,
        navText: ["<img src='img/prev.svg'>", "<img src='img/next.svg'>"],
        responsiveClass: true,
    });
    $('li.forHover').hover(function () {
        $('a.hover').addClass('hover-element');
        $('.hover-pre-point').addClass('show');
        $('div.wrap').fadeIn();
    }, function () {
        $('a.hover').removeClass('hover-element');
        $('.hover-pre-point').removeClass('show');
        $('div.wrap').fadeOut();
    });
    $('div.hover-pre-point').hover(function () {
        $(this).parent().find('a.hover').addClass('hover-element');
    }, function () {
        $('a.hover').removeClass('hover-element');
    });
    $('.menu-open .close').on('click', function () {
        $('.contacts').fadeOut(1);
        $(this).removeClass('show');
        $('.open').addClass('show');
        $('.menu-tablet').css({
            display: 'flex'
        })
    });
    $('.menu-open .open').on('click', function () {
        $('.contacts').fadeIn(1);
        $(this).removeClass('show');
        $('.close').addClass('show');
        $('.menu-tablet').css({
            display: 'none'
        })
    });
    $('.tools .open').on('click', function () {
        $(this).parent().parent().css({
            flexGrow: 2
        })
        $(this).parent().parent().parent().parent().find('.logo-mobile').fadeOut(1);
        $(this).parent().parent().find('.contacts').fadeOut(5);
        $(this).parent().parent().find('.call').fadeOut(5);
        $('#shop').fadeOut(5);
        $(this).removeClass('show');
        $('.tools .close').addClass('show');
        $('.search').fadeIn(10);
        $(this).parent().parent().parent().find('.menu-open .close').fadeOut(1);
        $(this).parent().parent().parent().find('.menu-mobile .close').removeClass('show');
        $(this).parent().parent().parent().find('.menu-mobile .open').addClass('show');
    });
    $('.tools .close').on('click', function () {
        $(this).parent().parent().css({
            flexGrow: 0
        })
        $(this).parent().parent().find('.contacts').fadeIn(50);
        $(this).parent().parent().find('.call').fadeIn(50);
        $('#shop').fadeIn(50);
        $(this).removeClass('show');
        $('.tools .open').addClass('show');
        $('.search').fadeOut(1);
    });
    $('.search .open').on('click', function () {
        $(this).parent().parent().parent().parent().css({
            flexGrow: 0
        });
        $(this).parent().parent().parent().parent().find('.contacts').fadeIn(50);
        $(this).parent().parent().parent().parent().find('.call').fadeIn(50);
        $('#shop').fadeIn(50);
        $(this).removeClass('show');
        $('.tools .open').addClass('show');
        $('.search').fadeOut(1);
        $(this).parent().parent().parent().parent().parent().find('.menu-open .close').fadeIn(1);
    });
    $('.menu-mobile .open').on('click', function() {
        $(this).parent().parent().parent().find('.tool').css({
            flexGrow: 0
        });
        $(this).removeClass('show');
        $('.menu-mobile .close').addClass('show');
        $(this).parent().parent().parent().find('.logo-mobile').fadeIn(1);
        $(this).parent().parent().parent().find('.search').fadeOut(1);
        $(this).parent().parent().parent().find('.tools #shop').fadeIn(1);
        $(this).parent().parent().parent().find('.tools .open').addClass('show');
    });
    $('li.open-mobile').on('click', function() {
        $('.drop').slideToggle();
        $('img.item').toggleClass('rotate');
    })
    $('.menu-mobile .close').on('click', function() {
        $(this).removeClass('show');
        $('.open').addClass('show');
        $(this).parent().parent().parent().parent().parent().find('.menu-phone').slideDown();
        $(this).parent().parent().parent().parent().parent().animate({
            height: '110vh'
        });
        $(this).parent().parent().parent().parent().parent().parent().parent().css({
            overflowY : 'hidden'
        });
    });
    $('.menu-mobile .open').on('click', function() {
        $(this).removeClass('show');
        $('.close').addClass('show');
        $(this).parent().parent().parent().parent().parent().find('.menu-phone').slideUp();
        $(this).parent().parent().parent().parent().parent().animate({
            height: '1%'
        });
        $(this).parent().parent().parent().parent().parent().parent().parent().css({
            overflowY : 'scroll'
        });
    });
});